import { useTranslation } from "react-i18next";
import { useDepartmentAllUniversalBatch } from "../../../Hooks/ApiHooks/SiteManager/site-manager-api";
import Loading from "../../../Loader/Loading";
import CustomSelectUniversalBatch from "../../../Ui/Select/CustomSelectUniversalBatch";

const UniversalBatchDorpdown = ({
  departmentId,
  onSelect,
  errorShow,
  defalutValue,
  isDenied,
  customStyleContainer,
}) => {
  const { t } = useTranslation();
  const { departmentAllUniversalBatch, departmentAllUniversalBatchLoading } =
    useDepartmentAllUniversalBatch({
      data: {
        did: departmentId,
        page: 1,
        limit: 1000,
        search: "",
      },
      skip: !departmentId,
    });
  return (
    <>
      <CustomSelectUniversalBatch
        selectLabel={t("select_universal_batch")}
        selectedValue={t("select_universal_batch_placeholder")}
        options={
          departmentAllUniversalBatch?.list?.length > 0
            ? departmentAllUniversalBatch?.list
            : []
        }
        onClick={onSelect}
        isFull
        errorShow={errorShow}
        defalutValue={defalutValue}
        isDenied={isDenied}
        customStyleContainer={customStyleContainer}
      />

      {/* {departmentAllUniversalBatchLoading && <Loading />} */}
    </>
  );
};

export default UniversalBatchDorpdown;
